import React from 'react'
import cx from 'classnames'
import { Form, Row, Col } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import { ModalProps } from 'antd/lib/modal'
import { LinkOutlined } from '@ant-design/icons'
import { head, isNil, is, of, identity } from 'ramda'
import { FormItem } from '@/components/form'
import Modal from '@/components/modal'
import { Input } from '@/components/input'
import Table from '@/components/query-table'
import Button from '@/components/button'
import { useRadlexes } from '@/services/radlex'
import useTableRowSelection from '@/hooks/useTableRowSelection'
import useVisible from '@/hooks/useVisible'
const { compose } = require('ramda')

interface RadlexProps {
  onSelect?: (value: { radlexCode: string }) => void
}

export function useRadlex(onOk?: (value: { radlexCode: string }) => void) {
  return (value: { radlexCode: string }) => {
    if (onOk) {
      onOk(value)
    }
  }
}

export function Radlex({ onSelect }: RadlexProps) {
  const [radlexModalVisible, handleRadlexModalVisible] = useVisible()

  const [code, setCode] = React.useState<string>()

  const handleSelect = (radlex: { id: number; code: string }) => {
    if (onSelect) {
      onSelect({ radlexCode: radlex?.code })
    }
    setCode(radlex.code)
    handleRadlexModalVisible()
  }

  return (
    <>
      {radlexModalVisible && (
        <RadlexListModal
          selected={code}
          onOk={handleSelect}
          onCancel={handleRadlexModalVisible}
        />
      )}
      <FormItem label="Text" className="is--flex">
        <Form.Item noStyle name="text">
          <Input className={cx({ 'is--linked': !isNil(code) })} />
        </Form.Item>
        <Form.Item noStyle>
          <LinkOutlined
            className="ml-2 cursor-pointer"
            onClick={handleRadlexModalVisible}
          />
        </Form.Item>
      </FormItem>
    </>
  )
}

interface RadlexModalProps extends Omit<ModalProps, 'onOk'> {
  onOk(value: { code: string; id: number }): void
  selected?: string
}

export function RadlexListModal({
  onOk,
  onCancel,
  selected,
  ...props
}: RadlexModalProps) {
  const { dataSource, loading } = useRadlexes()

  const [keyword, setKeyword] = React.useState<string>()

  const { rowSelection, selectedRowCount, selectedRows } = useTableRowSelection(
    {
      multiple: false,
      ...(selected
        ? {
            defaultRowKeys: compose(is(Array), identity, of)(selected),
          }
        : {}),
    }
  )

  const columns: ColumnProps<any>[] = [
    {
      title: 'RadLex Codes',
      dataIndex: 'code',
    },
  ]

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    setKeyword(evt.target.value)
  }

  const handleOk = () => {
    if (selectedRowCount > 0) {
      onOk(head(selectedRows))
    }
  }

  let _dataSource = {
    ...dataSource,
    content: dataSource.content.filter(x =>
      keyword ? x.code.includes(keyword) : true
    ),
  }

  return (
    <Modal
      {...props}
      visible
      title="選擇 Radlex code"
      bodyStyle={{
        paddingTop: 48,
        paddingLeft: 64,
        paddingRight: 64,
      }}
      width={650}
      footer={[
        <Button key="btn-cancel" color="outline" onClick={onCancel}>
          取消
        </Button>,
        <Button
          key="btn-ok"
          color="primary"
          onClick={handleOk}
          className="ml-3">
          確認
        </Button>,
      ]}
      onCancel={onCancel}
      keyboard={false}
      maskClosable={false}>
      <Row>
        <Col span={24}>
          關鍵字:
          <Input
            className="ml-2 w-2/3"
            placeholder="輸入 Radlex code"
            onChange={handleChange}
          />
        </Col>
      </Row>
      <Row className="mt-6">
        <Col span={24}>
          <Table
            rowKey="code"
            size="small"
            dataSource={_dataSource}
            loading={loading}
            pagination={false}
            columns={columns}
            rowSelection={rowSelection}
          />
        </Col>
      </Row>
    </Modal>
  )
}
