import React from 'react'
import {
  mergeAll,
  map,
  values,
  head,
  pathOr,
  flatten,
  length,
  of,
  filter,
  keys,
  always,
  isNil,
  ifElse,
  invoker,
  is,
  identity,
  equals,
  __,
  anyPass,
  assoc,
} from 'ramda'
import { setComponentByTab } from '@/components/snippet-template-list'
import {
  Snippets,
  SnippetProps,
  ReportTemplate,
  MutationOptions,
  Mutation,
  QueryVariables,
  ReportTemplateQueryResult,
} from '@/constants/types'
import useSWR from '@/hooks/useSWR'
import { useRequest } from '@/utils/request'
import { toMoment, toDateString } from '@/utils/webHelper'
import { statusEnums } from '@/constants/enums'
const { compose } = require('ramda')

// 模版分類
export const snippetCategoryOfReport = {
  PATHOLOGICAL_DIAGNOSIS: 'Pathological diagnosis',
  GROSS: 'Gross',
  MICROSCOPY: 'Microscopy',
  NOTES: 'Notes',
  REFERENCES: 'References',
}

const defaultSnippetsOfReport = compose(
  mergeAll,
  map((key: string) => ({ [key]: [] })),
  values
)(snippetCategoryOfReport)

const convertData = compose(
  setComponentByTab,
  map((x: ReportTemplateQueryResult) => ({
    ...x,
    id: x.no,
    name: x.templateName,
    description: x.templateDescription,
    setting: compose(
      (setting: Snippets) =>
        compose(
          mergeAll,
          map((key: string) => ({
            [key]: compose(
              map((s: SnippetProps) => ({
                ...s,
                ...(s.inline ? { html: decodeURIComponent(s.html) } : {}),
              })),
              pathOr([], [key])
            )(setting),
          })),
          keys
        )(setting),
      ifElse(
        isNil,
        always(defaultSnippetsOfReport),
        compose(
          ifElse(is(Object), identity, always(defaultSnippetsOfReport)),
          ifElse(
            is(String),
            val => {
              try {
                return invoker(1, 'parse')(val)(JSON)
              } catch (error) {
                return defaultSnippetsOfReport
              }
            },
            identity
          ),
          ifElse(
            anyPass([equals(__, 'undefined'), equals(__, '[object Object]')]),
            always(defaultSnippetsOfReport),
            identity
          )
        )
      )
    )(x.templateJson),
    statusName: pathOr('草稿', [x.status], statusEnums),
    createdAt: toMoment(x.createDate),
    createdAtStr: toDateString(x.createDate, 'YYYY.MM.DD HH:mm'),
    updatedAt: toMoment(x.updateDate),
    updatedAtStr: toDateString(x.updateDate, 'YYYY.MM.DD HH:mm'),
  })),
  pathOr([], [])
)

// 報告模版清單
export function useUserReportTemplates({
  id = 0,
  division = 'PATH',
  ...params
}: QueryVariables = {}) {
  // const [{state, contents}, setReportTemplates] = useRecoilStateLoadable(
  //   test({ id: user.id, division: user.division }) as any
  // )
  // console.log(state)
  // return useRecoilState(reportTemplateListState)

  const { data, ...rest } = useSWR<ReportTemplateQueryResult[]>(
    `/api/v1/c4/getReportTemplateList/{id}&{division}`,
    { variables: { id, division, ...params } }
  )

  return {
    ...rest,
    data: convertData(data) as ReportTemplate[],
  }
}

export function useAllReportTemplates({
  id = 0,
  division = 'PATH',
  ...params
}: QueryVariables = {}) {
  const { data, ...rest } = useSWR<ReportTemplateQueryResult[]>(
    `/api/v1/c1/getCompleteReportTemplateList/{id}&{division}`,
    { variables: { id, division, ...params } }
  )

  return {
    ...rest,
    data: convertData(data) as ReportTemplate[],
  }
}

// 取得報告模版明細資料
export function useReportTemplate({
  userId = 0,
  division = 'PATH',
  name = '',
  data,
  id,
  ...params
}: {
  [key: string]: any
  userId?: React.Key
  division?: string
  name?: string
  data?: any
}) {
  const response = useSWR<ReportTemplateQueryResult[]>(
    `/api/v1/c4/getReportTemplate/{userId}&{division}&{name}`,
    { variables: { userId, division, name, ...params } }
  )

  const _data = compose(
    head,
    pathOr([], ['data'])
  )(response) as ReportTemplateQueryResult

  const [reportTemplate, setReportTemplate] = React.useState<ReportTemplate>({
    setting: defaultSnippetsOfReport,
  } as any)

  React.useEffect(() => {
    if (data?.id) {
      setReportTemplate(compose(head, setComponentByTab, of)(data))
    }
  }, [data])

  React.useEffect(() => {
    if (_data) {
      setReportTemplate(
        compose(assoc('author', 'Chonny'), head, convertData, of)(_data)
      )
    }
  }, [_data])

  let _snippetList = pathOr(defaultSnippetsOfReport, ['setting'])(
    reportTemplate
  )

  React.useEffect(() => {
    setSnippetList(_snippetList)
  }, [JSON.stringify(_snippetList)]) // eslint-disable-line

  const [snippetList, setSnippetList] = React.useState<Snippets>(_snippetList)

  let snippetById: { [key: string]: SnippetProps } = {}

  return {
    ...response,
    reportTemplate,
    snippetList,
    setSnippetList,
    categoryCountById: compose(
      mergeAll,
      map((key: string) => ({ [key]: length(snippetList[key]) })),
      keys
    )(snippetList),
    categoryCount: compose(length, keys)(snippetList),
    total: compose(
      length,
      filter((x: SnippetProps) => x.type !== 'TemplateSectionHeader'),
      flatten,
      values
    )(snippetList),
    snippetById: compose(
      map((x: SnippetProps) => (snippetById[x.id] = x)),
      flatten,
      values
    )(snippetList),
  }
}

export function useCreateOrUpdateReportTemplateAPI(
  options: MutationOptions = {}
) {
  const { loading, mutate } = useRequest(`/api/v1/c4/createReportTemplate`, {
    ...options,
    method: 'POST',
  })

  return [loading, mutate] as Mutation
}

export function useDeleteReportTemplateAPI(options: MutationOptions = {}) {
  const { loading, mutate } = useRequest(`/api/v1/c4/delectReportTemplate`, {
    ...options,
    method: 'POST',
  })

  return [loading, mutate] as Mutation
}
