import React from 'react'
import { atom, useRecoilState } from 'recoil'
import {
  assoc,
  map,
  head,
  pathOr,
  of,
  ifElse,
  is,
  always,
  invoker,
  identity,
  equals,
  __,
  anyPass,
} from 'ramda'
import Store from 'store'
import { setComponent } from '@/components/snippet-template-list'
import {
  Mutation,
  MutationOptions,
  SnippetProps,
  PhraseTemplate,
  QueryVariables,
  ReportTemplateQueryResult,
} from '@/constants/types'
import useSWR from '@/hooks/useSWR'
import { useRequest } from '@/utils/request'
import { toMoment, toDateString, isNil } from '@/utils/webHelper'
import { statusEnums } from '@/constants/enums'
const { compose } = require('ramda')

const fakePhraseTemplateList: PhraseTemplate[] = setComponent(
  Store.get('phraseTemplateListState')
)

const phraseTemplateListState = atom({
  key: 'phraseTemplateListState',
  default: fakePhraseTemplateList,
})

// 片語模版清單
export function usePhraseTemplates() {
  return useRecoilState(phraseTemplateListState)
}

const convertData = compose(
  setComponent,
  map((x: ReportTemplateQueryResult) => ({
    ...x,
    id: x.no,
    name: x.templateName,
    description: x.templateDescription,
    setting: compose(
      map((s: SnippetProps) => ({
        ...s,
        ...(s.inline ? { html: decodeURIComponent(s.html) } : {}),
      })),
      ifElse(
        isNil,
        always([]),
        compose(
          ifElse(is(Array), identity, always([])),
          ifElse(
            is(String),
            val => {
              try {
                return invoker(1, 'parse')(val)(JSON)
              } catch (error) {
                return []
              }
            },
            identity
          ),
          ifElse(
            anyPass([
              equals(__, 'undefined'),
              equals(__, '[object Object]'),
              isNil,
            ]),
            always([]),
            identity
          )
        )
      )
    )(x.templateJson),
    statusName: pathOr('草稿', [x.status], statusEnums),
    createdAt: toMoment(x.createDate),
    createdAtStr: toDateString(x.createDate, 'YYYY.MM.DD HH:mm'),
    updatedAt: toMoment(x.updateDate),
    updatedAtStr: toDateString(x.updateDate, 'YYYY.MM.DD HH:mm'),
  })),
  pathOr([], [])
)

// 報告模版清單
export function useUserPhraseTemplates({
  id = 0,
  division = 'PATH',
  ...params
}: QueryVariables = {}) {
  const { data, ...rest } = useSWR<ReportTemplateQueryResult[]>(
    `/api/v1/c5/getPhraseTemplateList/{id}&{division}`,
    { variables: { id, division, ...params } }
  )

  return {
    ...rest,
    data: convertData(data) as PhraseTemplate[],
  }
}

export function useAllPhraseTemplates({
  id = 0,
  division = 'PATH',
  ...params
}: QueryVariables = {}) {
  const { data, ...rest } = useSWR<ReportTemplateQueryResult[]>(
    `/api/v1/c5/getCompletePhraseTemplateList/{id}&{division}`,
    { variables: { id, division, ...params } }
  )

  return {
    ...rest,
    data: convertData(data) as PhraseTemplate[],
  }
}

// 片語模版資料
export function usePhraseTemplate({
  userId = 0,
  division = 'PATH',
  name = '',
  data,
  id,
  ...params
}: {
  [key: string]: any
  userId?: React.Key
  division?: string
  name?: string
  data?: any
}) {
  const response = useSWR<ReportTemplateQueryResult[]>(
    `/api/v1/c5/getPhraseTemplate/{userId}&{division}&{name}`,
    { variables: { userId, division, name, ...params } }
  )

  const _data = compose(
    head,
    pathOr([], ['data'])
  )(response) as ReportTemplateQueryResult

  const [phraseTemplate, setPhraseTemplate] = React.useState<PhraseTemplate>({
    setting: [],
  } as any)

  React.useEffect(() => {
    if (data?.id) {
      setPhraseTemplate(compose(head, setComponent, of)(data))
    }
  }, [data])

  React.useEffect(() => {
    if (_data) {
      setPhraseTemplate(
        compose(assoc('author', 'Chonny'), head, convertData, of)(_data)
      )
    }
  }, [_data])

  let _snippetList = pathOr([], ['setting'])(phraseTemplate)

  React.useEffect(() => {
    setSnippetList(_snippetList)
  }, [JSON.stringify(_snippetList)]) // eslint-disable-line

  const [snippetList, setSnippetList] = React.useState<SnippetProps[]>(
    _snippetList
  )

  let snippetById: { [key: string]: SnippetProps } = {}

  return {
    phraseTemplate,
    snippetList,
    setSnippetList,
    snippetById: map((x: SnippetProps) => (snippetById[x.id] = x), snippetList),
  }
}

export function useCreateOrUpdatePhraseTemplateAPI(
  options: MutationOptions = {}
) {
  const { loading, mutate } = useRequest(`/api/v1/c5/createPhraseTemplate`, {
    ...options,
    method: 'POST',
  })

  return [loading, mutate] as Mutation
}

export function useDeletePhraseTemplateAPI(options: MutationOptions = {}) {
  const { loading, mutate } = useRequest(`/api/v1/c5/delectPhraseTemplate`, {
    ...options,
    method: 'POST',
  })

  return [loading, mutate] as Mutation
}
