import React from 'react'
import { atom, useRecoilState } from 'recoil'
import moment from 'moment'
import {
  head,
  values,
  mergeAll,
  map,
  last as Rlast,
  filter,
  omit,
  keys,
  sort,
  inc,
  pathOr,
  length,
  ifElse,
  always,
  isNil,
} from 'ramda'
import { SelectProps } from 'antd/lib/select'
import {
  Mutation,
  MutationOptions,
  SnippetProps,
  ReportTemplate,
  ReportTemplateSetting,
  QueryVariables,
} from '@/constants/types'
import {
  useReportTemplate as useReportTemplateItem,
  useUserReportTemplates,
  useAllReportTemplates,
  useCreateOrUpdateReportTemplateAPI,
  useDeleteReportTemplateAPI,
} from '@/data/useReportTemplate'
import { getNumberFromString, downloadAsJson } from '@/utils/webHelper'
import Select from '@/components/select'
import useAuth from '@/hooks/useAuth'
import { confirm } from '@/utils/message'
const { compose } = require('ramda')

const snippetCategoryOfReport = {
  PATHOLOGICAL_DIAGNOSIS: 'Pathological diagnosis',
  GROSS: 'Gross',
  MICROSCOPY: 'Microscopy',
  NOTES: 'Notes',
  REFERENCES: 'References',
}

export function useUpdateSnippet<T = ReportTemplateSetting>(options: {
  item: SnippetProps
  onUpdate: (data: any) => void
  defaultValue?: any
}) {
  const { item, onUpdate, defaultValue = {} } = options

  const [setting, setSetting] = React.useState<T>(
    pathOr(defaultValue, ['setting'], item) as T
  )

  const handleOk = (values: T) => {
    const newSetting = { ...setting, ...values }
    setSetting(newSetting)
    onUpdate({ ...item, setting: newSetting })
  }

  return { setting, handleOk }
}

export const useReportTemplates = (variables: QueryVariables = {}) => {
  const { user } = useAuth()
  const { data, ...rest } = useUserReportTemplates({ ...user, ...variables })

  const dataSource = {
    content: data,
    total: length(data),
    totalPages: Math.floor(length(data) / 10) + 1,
  }

  return {
    ...rest,
    dataSource,
    data: dataSource,
  }
}

export const ReportTemplateSelect = ({
  onChange,
  onSearch,
  ...props
}: SelectProps<any>) => {
  const [keyword, setKeyword] = React.useState<string>()

  const { user } = useAuth()
  const { data } = useAllReportTemplates(user)

  const filterReportTemplates: ReportTemplate[] = ifElse(
    isNil,
    always(data),
    val => filter((x: ReportTemplate) => x.name.includes(val))(data)
  )(keyword)

  const handleSearch = (value: string) => {
    setKeyword(value)
  }

  const handleChange = (value: string) => {
    if (onChange) {
      ;(onChange as any)(value)
    }
  }

  return (
    <Select
      showSearch
      className="w-2/3"
      bordered={false}
      filterOption={false}
      onSearch={handleSearch}
      onChange={handleChange}
      {...props}>
      {filterReportTemplates.map(x => (
        <Select.Option key={x.id} value={x.name}>
          {x.name}
        </Select.Option>
      ))}
    </Select>
  )
}

export const useReportTemplate = (variables: { id?: string; data?: any }) => {
  const { user } = useAuth()
  const { setSnippetList, snippetList, ...rest } = useReportTemplateItem({
    ...variables,
    userId: user.id,
    division: user.division,
    name: variables.id,
  })

  const [category, setCategory] = useCurrentCategoryOfSnippet()

  const handleSnippetUpdate = (data: any[]) => {
    setSnippetList(prev => ({ ...prev, [category]: data }))
  }

  const handleTabChange = (activeKey: string) => {
    setCategory(activeKey)
  }

  const handleTabMove = (order: string[]) => {
    const snippetKeys = keys(snippetList)

    setSnippetList(
      compose(
        mergeAll,
        map((key: string) => ({ [key]: snippetList[key] })),
        sort((a: string, b: string) => {
          const orderA = order.indexOf(a)
          const orderB = order.indexOf(b)

          if (orderA !== -1 && orderB !== -1) {
            return orderA - orderB
          }
          if (orderA !== -1) {
            return -1
          }
          if (orderB !== -1) {
            return 1
          }

          const ia = snippetKeys.indexOf(a)
          const ib = snippetKeys.indexOf(b)

          return ia - ib
        }),
        keys
      )(snippetList)
    )
  }

  const handleTabAdd = () => {
    const last = compose(
      inc,
      pathOr(0, []),
      getNumberFromString,
      Rlast,
      sort((a: any, b: any) => a - b),
      keys
    )(snippetList)

    setSnippetList(prev => ({ ...prev, [`新標籤頁${last}`]: [] }))
    setCategory(`新標籤頁${last}`)
  }

  const handleTabRemove = (targetKey: string) => {
    confirm({
      title: '確認要刪除此頁籤嗎？',
      content: '刪除會連同模版資料一起刪除，確認繼續？',
      onOk: () => {
        setSnippetList(prev => omit([targetKey], prev))

        if (category === targetKey) {
          setCategory(
            compose(
              Rlast,
              filter((key: string) => key !== targetKey),
              keys
            )(snippetList)
          )
        }
      },
    })
  }

  const handleTabNameChange = (targetKey: string) => (
    evt: React.FocusEvent<HTMLSpanElement>
  ) => {
    const newTabName = evt.target.innerHTML

    setSnippetList(prev =>
      compose(
        mergeAll,
        map((key: string) => ({
          [key === targetKey ? newTabName : key]: prev[key],
        })),
        keys
      )(prev)
    )
    setCategory(newTabName)
  }

  const handleTabEdit = (
    e:
      | string
      | React.MouseEvent<Element, MouseEvent>
      | React.KeyboardEvent<Element>,
    action: 'add' | 'remove'
  ) => {
    const handler = action === 'add' ? handleTabAdd : handleTabRemove

    handler(e as string)
  }

  return {
    ...rest,
    category,
    snippetList,
    setCategory,
    setSnippetList,
    handleTabEdit,
    handleTabChange,
    handleTabMove,
    handleTabAdd,
    handleTabRemove,
    handleTabNameChange,
    handleSnippetUpdate,
  }
}

export const useCreateOrUpdateTemplate = (options: MutationOptions = {}) => {
  const [loading, mutate] = useCreateOrUpdateReportTemplateAPI(options)

  const { user } = useAuth()

  const hanlder = (values: ReportTemplate) => {
    return mutate({
      ...values,
      userId: user.id,
      division: user.division,
      templateName: values.name,
      templateJson: JSON.stringify(
        compose(
          mergeAll,
          map((key: string) => ({
            [key]: compose(
              map((s: SnippetProps) => ({
                ...s,
                ...(s.inline ? { html: encodeURIComponent(s.html) } : {}),
              })),
              pathOr([], ['setting', key])
            )(values),
          })),
          keys
        )(values.setting)
      ),
      templateDescription: values.description,
    })
  }

  return [loading, hanlder] as Mutation
}

export const useDeleteTemplate = (options: MutationOptions = {}) => {
  const [loading, mutate] = useDeleteReportTemplateAPI(options)

  const { user } = useAuth()

  const hanlder = (values: ReportTemplate) => {
    return mutate({
      ...values,
      userId: user.id,
      division: user.division,
      TemplateNAME: values.name,
    })
  }

  return [loading, hanlder] as Mutation
}

export const useExportTemplate = ({
  data,
  filename,
}: {
  data: ReportTemplate
  filename: string
}) => {
  const handleExport = () => {
    downloadAsJson({
      filename: `${filename}_${data?.name || ''}_${moment().format(
        'YYYYMMDDHHmmss'
      )}`,
      json: data,
    })
  }

  return handleExport
}

const reportPreviewState = atom({
  key: 'reportPreviewState',
  default: false,
})

const categoryState = atom<string>({
  key: 'categoryState',
  default: compose(head, values)(snippetCategoryOfReport),
})

// 切換編輯/檢視模式
export function useReportPreview() {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}

// 更方便切換編輯/檢視模式
export function useToggleReportPreview(state: boolean = false) {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  React.useEffect(() => {
    setIsPreview(state)

    return () => {
      setIsPreview(false)
    }
  }, [state]) // eslint-disable-line

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}

// 編輯報告/模版時，當前的分類
export function useCurrentCategoryOfSnippet() {
  return useRecoilState(categoryState)
}
