import { atom, useRecoilState } from 'recoil'
import Store from 'store'
import { setComponentByTab } from '@/components/snippet-template-list'
import useSWR from '@/hooks/useSWR'
import { request } from '@/utils/request'
import { QueryVariables } from '@/constants/types'

export const reportListState = atom({
  key: 'reportListState',
  default: setComponentByTab(Store.get('reportListState') || []),
})

// 輸入報告清單
export function useReportList({
  classification = 'pathorderview',
  noType = 'ORREQNO',
  no = '1',
  ...variables
}: QueryVariables = {}) {
  const { data, ...rest } = useSWR(
    `/api/v1/b2/getPathologyResult`,
    { variables },
    url =>
      request(url, {
        method: 'POST',
        body: {
          ...variables,
          classification,
          noType,
          no,
        },
      })
  )

  console.log(data)

  // return {
  //   ..rest,
  //   data
  // }

  return useRecoilState(reportListState)
}
